      <span class="header_search" slot="left">
        <i class="iconfont icon-sousuo"></i>
      </span>
      <span class="header_login" slot="right">
        <span class="header_login_text">登录/注册</span>
      </span>
    </HeaderTop>
    <!--首页导航-->
    <nav class="msite_nav">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
            <a href="javascript:" class="link_to_fond">
              <div class="food_container">
                <img src alt />
                <span>xxx</span>
              </div>
            </a>
          </div>
          <div class="swiper-slide">轮播</div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
    </nav>
    <!--首页列表-->
    <shop-list />
  </div>
</template>

<script>
import HeaderTop from "../../components/HeaderTop/HeaderTop.vue";
import Swiper from "swiper";
import "swiper/css/swiper.min.css";
import ShopList from "../../components/ShopList/ShopList.vue";
export default {
  data() {
    return {
      location:'aa'
    };
  },
  async mounted() {
    //创建swiper对象实现轮播
    new Swiper(".swiper-container", {
      loop: true, //可以循环轮播
      pagination: {
        el: ".swiper-pagination" //分页器
      }
    });

  //   fetch('http://localhost:8080/found')
  //   .then(res=>{
  //     res.json();
     
  //  })
  //  .then(resp=>{
  //    this.location = resp.loct;
  //   console.log(resp.loct)
  
  //  }).catch(err=>{
  //    this.location = '获取位置失败';
  
  //  })
  var lo = '位置获取失败'
  await ajax({
    type:"get",
    url:"http://localhost:8080/found",
    success:function (data){
      lo = data.loct;
      console.log(data);
    },
    error:function (err){
     lo = '获取位置失败';
      console.log(err)
    }
  })
  this.location = lo;
  function ajax (obj) {
    // type 请求方式, url 地址, params 参数, fun 回调函数
      obj.type = obj.type.toUpperCase()            //在传入type的时候可以忽略大小写
      var xhr = new XMLHttpRequest();
      var timer = null;
    //参数也可以指直接写字符串形式 不能是汉字
      if (typeof obj.data === 'object') {        //如果在地址传入的东西是一个对象，我们将它的格式转化为urlencoded
        var tempArr = []
        for (var key in obj.data) {
          var value = obj.data[key]
          tempArr.push(encodeURIComponent(key) + '=' + encodeURIComponent(value))
        }
      obj.data = tempArr.join('&') //===>这里的格式就为parsms=[key1=value&key2=value2]
      }
  
      if (obj.type === 'GET') {
        obj.url += '?' + obj.data;
        xhr.open(obj.type, obj.url+"&t="+new Date().getTime(), true)
        xhr.send();
      }
  
      //true代表异步
  
  
      if (obj.type === 'POST') {    //如果请求的方式为POST，需要手动设置请求头的Content-Type
          xhr.open(obj.type, obj.url+"&t="+new Date().getTime(), true)
        xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded')
  
        xhr.send(obj.data);
      }
  
      xhr.onreadystatechange = function () {
        if (this.readyState === 4) {
            clearTimeout(timer);
         if(this.status >=200 && this.status<300 || this.status == 304){
          obj.success(this.responseText);
         }else{
             obj.error();
         }
        }
      }
  
      
      // 处理超时
      if(obj.time){
          timer = setTimeout(xhr.abort,obj.time)
      }
    }
  },
  
  components: {
    HeaderTop,
    ShopList
  }
};
</script>

<style scoped>
.msite_nav {
  width: 100%;
  height: 200px;

  box-sizing: border-box;
  position: absolute;
  top: 30px;
  left: 0;
}
.msite_nav  img{
  width: 50px;
  height: 50px;
  border:1px solid black;
  display: block;
  margin:10px 0 0 35px;
}
.swiper-container {
  width: 100%;
  height: 200px;
}
.swiper-slide {
  width: 100%;
  height: 200px;
  border: 1px solid black;
}
.swiper-slide a {
  width: 25%;
  height: 90px;

  float: left;
  text-align: center;
}
.header_list {
  width: 100%;
  height: 40px;
}
</style>
 



